import React from 'react';
import { Row } from 'reactstrap';

const PsalmsComparison = () => (
  // <Container>
    <Row>
      <h2>Psalms Comparison Chart</h2>
      <table>
        <tr>
          <th>Book</th>
          <th>Psalm Number</th>
          <th>Author</th>
          <th>Description</th>
          <th>First Verse</th>
          <th>Summary</th>
        </tr>
      </table>
    </Row>
  // </Container>
);

export default PsalmsComparison;



